<template>
  <div class="d-flex" :class="this.$store.state.menuToggle" id="wrapper">
    <TheSidemenu />
    <div id="page-content-wrapper">
      <TheTopmenu />
      <div class="container-fluid text-start">
        <p class="fs-6 m-2">افزودن کاربر جدید</p>
        <div v-if="error" class="alert alert-danger">
          {{ error }}
        </div>
        <form class="row">
          <div class="col-md-6 col-xs-12">
            <div class="form-floating mb-3">
              <input
                type="text"
                class="form-control"
                id="first_name"
                name="first_name"
                placeholder="آنوشا"
                v-model="formData.first_name"
              />
              <label for="first_name">نام *</label>
            </div>
          </div>

          <div class="col-md-6 col-xs-12">
            <div class="form-floating mb-3">
              <input
                type="text"
                class="form-control"
                id="last_name"
                name="last_name"
                placeholder="آنوشا"
                v-model="formData.last_name"
              />
              <label for="last_name">نام خانوادگی *</label>
            </div>
          </div>

          <div class="col-md-6 col-xs-12">
            <div class="form-floating mb-3">
              <input
                type="text"
                class="form-control"
                id="email"
                name="email"
                placeholder="09153068145"
                v-model="formData.email"
              />
              <label for="email">تلفن همراه (نام کاربری) *</label>
            </div>
          </div>

          <div class="col-md-6 col-xs-12">
            <div class="form-floating mb-3">
              <input
                type="text"
                class="form-control"
                id="address"
                name="address"
                placeholder="نشانی"
                v-model="formData.address"
              />
              <label for="address">نشانی</label>
            </div>
          </div>

          <div class="col-md-6 col-xs-12">
            <div class="form-floating mb-3">
              <input
                type="password"
                class="form-control"
                id="password"
                name="password"
                placeholder="رمز عبور"
                v-model="formData.password"
              />
              <label for="password">رمز عبور *</label>
            </div>
          </div>

          <div class="col-md-6 col-xs-12">
            <div class="form-floating mb-3">
              <input
                type="password"
                class="form-control"
                id="password_confirmation"
                name="password_confirmation"
                placeholder="تکرار رمز عبور"
                v-model="formData.password_confirmation"
              />
              <label for="password_confirmation">تکرار رمز عبور *</label>
            </div>
          </div>

          <div class="col-md-6 col-xs-12">
            <div class="form-floating mb-3">
              <input
                type="text"
                class="form-control"
                id="postall"
                name="postall"
                placeholder="کدپستی"
                v-model="formData.postall"
              />
              <label for="postall">کدپستی</label>
            </div>
          </div>

          <div class="col-md-6 col-xs-12">
            <div class="form-floating mb-3">
              <input
                type="text"
                class="form-control"
                id="referrer_users_id"
                name="referrer_users_id"
                placeholder="معرف"
                v-model="formData.referrer_users_id"
              />
              <label for="referrer_users_id">معرف</label>
            </div>
          </div>

          <div class="col-md-6 col-xs-12">
            <div class="form-floating mb-3">
              <select
                class="form-control"
                id="provinces"
                name="provinces"
                @change="changeCities($event)"
              >
                <option value=""></option>
                <option
                  v-for="item in provinces"
                  :key="item.id"
                  :value="item.id"
                  :selected="item.id == formData.provinces_id"
                >
                  {{ item.name }}
                </option>
              </select>
              <label for="provinces">استان</label>
            </div>
          </div>

          <div class="col-md-6 col-xs-12">
            <div class="form-floating mb-3">
              <select
                class="form-control"
                id="cities_id"
                name="cities_id"
                v-model="formData.cities_id"
              >
                <option value=""></option>
                <option
                  v-for="item in cities"
                  :key="item.id"
                  :value="item.id"
                  :selected="item.id == formData.cities_id"
                >
                  {{ item.name }}
                </option>
              </select>
              <label for="cities_id">شهر</label>
            </div>
          </div>

          <div class="col-md-6 col-xs-12">
            <div class="form-floating mb-3">
              <select
                class="form-control"
                id="groups_id"
                name="groups_id"
                v-model="formData.groups_id"
              >
                <option value=""></option>
                <option
                  v-for="item in groups"
                  :key="item.id"
                  :value="item.id"
                  :selected="item.id == this.formData.groups_id"
                >
                  {{ item.name }}
                </option>
              </select>
              <label for="groups_id">گروه کاربری *</label>
            </div>
          </div>
        </form>
        <div>
          <a
            v-if="showSaveBtn"
            class="btn btn-primary btn-block"
            @click="addEditUser()"
          >
            ثبت
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TheSidemenu from "../components/TheSidemenu";
import TheTopmenu from "../components/TheTopmenu";
import ArefApi from "../libs/api.js";

export default {
  name: "AddUser",
  components: {
    TheSidemenu,
    TheTopmenu,
  },
  data() {
    return {
      error: null,
      showSaveBtn: true,
      formData: {
        id: this.$route.params.userId ? this.$route.params.userId : null,
        first_name: null,
        last_name: null,
        email: null,
        referrer_users_id: null,
        address: null,
        postall: null,
        cities_id: null,
        groups_id: null,
        password: null,
        password_confirmation: null,
        provinces_id: null,
      },
      cities: null,
      arefApi: new ArefApi(this),
      provinces: null,
      groups: null,
    };
  },
  methods: {
    addEditUser: function () {
      if (!this.validateFrm()) {
        return;
      }
      const fn = this.formData.id ? "editUser" : "addUser";
      this.formData.referrer_users_id = this.formData.referrer_users_id
        ? Number(this.formData.referrer_users_id)
        : this.formData.referrer_users_id;
      this.arefApi[fn](this.formData)
        .then(() => {
          this.$swal
            .fire({
              title: "",
              text: `ثبت کاربر با موفقیت انجام شد`,
              icon: "success",
              confirmButtonText: "متوجه شدم",
            })
            .then(() => {
              this.$router.push({ name: "ListUsers" });
            });
        })
        .catch((err) => {
          let alertText = "";
          if (err.data.errors) {
            for (let i in err.data.errors) {
              alertText += (alertText ? "" : ",") + `${err.data.errors[i]} `;
            }
          }
          this.$swal.fire({
            title: "!خطا",
            text: `${alertText}`,
            icon: "error",
            confirmButtonText: "متوجه شدم",
          });
        });
    },
    validMobile: function (mobile) {
      mobile = mobile.trim();
      if (mobile.indexOf(" ") > -1) {
        return false;
      }
      if (
        mobile.length == 11 &&
        mobile.charAt(0) == 0 &&
        mobile.charAt(1) == 9
      ) {
        return true;
      }
      return false;
    },
    validateFrm: function () {
      this.error = null;
      if (!this.formData.first_name || this.formData.first_name.length < 3) {
        this.error = "نام باید بیشتر از دو حرف باشد";
        return false;
      }

      if (!this.formData.last_name || this.formData.last_name.length < 3) {
        this.error = "نام خانوادگی باید بیشتر از دو حرف باشد";
        return false;
      }

      if (!this.formData.email) {
        this.error = " نام کاربری را صحیح وارد کنید";
        return false;
      }

      if (!this.formData.id) {
        if (!this.formData.password) {
          this.error = "رمز عبور را وارد نمایید";
          return false;
        }

        if (!this.formData.password_confirmation) {
          this.error = "تکرار رمز عبور را وارد نمایید";
          return false;
        }
      }

      if (this.formData.password != this.formData.password_confirmation) {
        this.error = "رمز عبور با تکرار آن برابر نمی باشد";
        return false;
      }

      if (Number(this.formData.groups_id) <= 0) {
        this.error = "گروه کاربری را انتخاب فرمایید";
        return false;
      }

      return true;
    },
    loadUserInfo: function () {
      return new Promise((resolve, reject) => {
        if (this.formData.id) {
          this.arefApi
            .getUser(this.formData.id)
            .then((resp) => {
              this.formData = resp;
              this.formData.groups_id = resp.group ? resp.group.id : null;
              this.formData.cities_id = resp.city ? resp.city.id : null;
              this.formData.provinces_id = resp.city
                ? resp.city.provinces_id
                : null;
              this.formData.referrer_users_id = resp.referrer_user
                ? Number(resp.referrer_user.id)
                : null;
              resolve();
            })
            .catch((err) => {
              this.$swal
                .fire({
                  title: "خطا در نمایش اطلاعات کاربر!",
                  text: "کاربر پیدا نشد ",
                  icon: "error",
                })
                .then(() => {
                  this.$router.push({ name: "ListUsers" });
                });
              reject(err);
            });
        }
      });
    },
    getCities: async function () {
      this.cities = await this.arefApi.getCities();
    },
    getProvinces: async function () {
      const res = await this.arefApi.getProvinces();
      this.provinces = res.data;
    },
    changeCities: async function (event) {
      const res = await this.arefApi.GetCitiesOfAProvince(event.target.value);
      this.cities = res.data;
    },
    getGroups: async function () {
      const res = await this.arefApi.getGroups();
      this.groups = res.data;
    },
  },
  mounted() {
    this.loadUserInfo().finally(() => {
      this.getCities();
    });
    this.getProvinces();
    this.getGroups();
  },
};
</script>

